.home{
    display: flex;
    
    justify-content: center;
    
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    flex-direction: column;

}

.amazon_banner_image01{
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
}
.home__row{
    display: flex;
    flex-wrap: wrap;
    justify-content:center ;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;

}